<template>
	<div>
		<el-dialog title="新建部门" :visible.sync="dialogFormVisible" label-width="80px" size="mini">
			<el-form :model="form">
				<el-form-item label="上级部门" :label-width="formLabelWidth">
					<el-input v-model="form.name" readonly="readonly" autocomplete="on"></el-input>
				</el-form-item>
				<el-form-item label="新添部门" :label-width="formLabelWidth">
					<el-input v-model="form.nname" autocomplete="on"></el-input>
				</el-form-item>
				<el-form-item label="duoxuan" :label-width="formLabelWidth">
					<el-checkbox-group v-model="type" size="small">
						<el-checkbox label="备选项1" border></el-checkbox>
						<el-checkbox label="备选项2" border></el-checkbox>
					</el-checkbox-group>
				</el-form-item>
				<el-form-item label="活动时间">
					<el-col :span="11">
						<el-date-picker value-format="yyyy-MM-dd" type="date" placeholder="选择日期" v-model="form.date1" style="width: 100%;"></el-date-picker>
					</el-col>
					<el-col :span="11">
						<el-date-picker type="date" placeholder="选择日期" v-model="form.date2" style="width: 100%;"></el-date-picker>
					</el-col>
				</el-form-item>
				<!-- <el-form-item label="duoxuan" >
					<el-checkbox-group v-model="form.type" size="small">
						<el-checkbox label="备选项1" border></el-checkbox>
						<el-checkbox label="备选项2" border ></el-checkbox>
					</el-checkbox-group>
				</el-form-item> -->

				<el-upload ref='up' :http-request='upl' class="upload-demo" drag action="http://localhost:8080/upload" multiple
				 :auto-upload='false'>
					<i class="el-icon-upload"></i>
					<div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
					<div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
				</el-upload>

			</el-form>


			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogFormVisible = false">取 消</el-button>
				<el-button type="primary" @click="()=>ck()">确 定</el-button>
			</div>
		</el-dialog>
	</div>
</template>
<script>
	import axios from 'axios'
	import $ from 'jquery'
	export default {
		name: 'treeadd',
		components: {

		},
		data: function() {
			return {
				dialogFormVisible: false,
				form: {},
				type: [],
				formLabelWidth: '120px',
				formdata: null
			}
		},
		methods: {
			upl: function(file) {
				if(this.formdata==null)
				{
					this.formdata = new FormData();
				}
				this.formdata.append('file',file.file)
				// this.formdata.append('file',file.file)
			},
			ck: function() {
				this.$refs.up.submit()
				let config = {
					headers: {
						'Content-Type': 'multipart/form-data'
					},
					params:this.form
				}

				axios.post('http://localhost:8080/upload', this.formdata, config).then(function(res) {
					console.log(res.data)
				})

// 				console.log(this.formdata)
				/* console.log(this.form)
				axios.get("http://localhost:8080/adds?type=" + this.type, {
					params: this.form
				}).then(function(res) {
					console.log('o')
				}).catch(function(res) {}) */
			},
			add: function() {
				var thiz = this
				axios.get("http://localhost:8080/add/" + this.form.id + '/' + this.form.nname).then(function(res) {
					if (res.data) {
						thiz.$message({
							message: '添加成功',
							type: 'success'
						});
						thiz.dialogFormVisible = false
						location.reload()
					}
				})
			}
		}
	}
</script>

<style>

</style>
