<template>
	<div>
		<el-dialog title="部门修改" :visible.sync="dialogFormVisible">
			<el-form :model="form">
				<el-form-item label="部门名称" :label-width="formLabelWidth">
					<el-input v-model="form.name" autocomplete="on"></el-input>
				</el-form-item>
			</el-form>


			<div slot="footer" class="dialog-footer">
				<el-button @click="dialogFormVisible = false">取 消</el-button>
				<el-button type="primary" @click="()=>upd()">确 定</el-button>
			</div>
		</el-dialog>
	</div>
</template>

<script>
	import axios from 'axios'
	import $ from 'jquery'
	export default {
		name: 'treeupd',
		components: {

		},
		data: function() {
			return {
				dialogFormVisible: false,
				form: {},
				formLabelWidth: '120px',
			}
		},
		methods: {
			upd: function() {
				var thiz = this
				axios.get("http://localhost:8080/upd/" + this.form.id + '/' + this.form.name).then(function(res) {
					if (res.data) {
						thiz.$message({
							message: '修改成功',
							type: 'success'
						});
						thiz.dialogFormVisible = false
						location.reload()
					}
				})
			}
		}
	}
</script>

