<template>
	<div class="custom-tree-container">
		<el-input placeholder="输入关键字进行过滤" v-model="filterText">
		</el-input>
		<el-tree :props="props" :load="loadNode" lazy show-checkbox @check="ck" @check-change="handleCheckChange"
		 :default-expanded-keys="[2, 3]" :default-checked-keys="[2,3]" node-key="id" ref='tree' accordion :filter-node-method="filterNode">
			<span class="custom-tree-node" slot-scope="{ node, data }">
				<span>{{ node.label }}</span>
				<span>
					<el-button type="text" size="mini" @click="() => upd(node, data)">
						修改
					</el-button>
					<el-button type="text" size="mini" @click="() => add(node, data)">
						添加
					</el-button>
					<el-button type="text" size="mini" @click="() => remove(node, data)">
						删除
					</el-button>
				</span>
			</span>
		</el-tree>
		<el-button @click='show'></el-button>
		<el-button @click="getCheckedNodes">通过 node 获取</el-button>
		<treeadd ref='add'></treeadd>
		<el-form ref="form" :model="s" label-width="80px">
			<el-form-item label="活动性质">
				<el-checkbox-group v-model="s.type">
					<el-checkbox-button label="美食/餐厅线上活动" name="type">啊啊啊</el-checkbox-button>
					<el-checkbox-button label="地推活动" name="type"></el-checkbox-button>
					<el-checkbox-button label="线下主题活动" name="type"></el-checkbox-button>
					<el-checkbox-button label="单纯品牌曝光" name="type"></el-checkbox-button>
				</el-checkbox-group>
			</el-form-item>
		</el-form>
		<el-form ref="form" :model="form" label-width="80px">
			<el-form-item label="新添部门">
				<el-input v-model="form.name" autocomplete="on"></el-input>
			</el-form-item>

		</el-form>

		<form method="post" action="http://localhost:8080/upload" enctype="multipart/form-data">
			<input type="file" name="file"><br>
			<input type="submit" value="提交">
		</form>

		<hr />
		<el-upload class="upload-demo" action="http://localhost:8080/upload" :on-preview="handlePreview" :on-remove="handleRemove"
		 :before-remove="beforeRemove" multiple :limit="3" :on-exceed="handleExceed" :file-list="fileList">
			<el-button size="small" type="primary">点击上传</el-button>
			<div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
		</el-upload>

		<input type="file" @change="tirggerFile($event)">
		<button @click="kk">aa</button>
	</div>

</template>

<script>
	import axios from 'axios'
	import $ from 'jquery'
	import treeadd from './treeadd.vue'
	export default {
		data() {
			return {
				props: {
					label: 'name',
					children: 'children'
				},
				filterText: '',
				s: {
					type: []
				},
				form: {},
				fileList: [],
				ff: null
			}
		},
		components: {
			treeadd
		},
		watch: {
			filterText(val) {
				this.$refs.tree.filter(val);
			},
			's.type': function(val) {
				axios.get("http://localhost:8080/adds?type=" + this.s.type, {
					params: this.form
				}).then(function(res) {
					console.log('o')
				}).catch(function(res) {})
			}
		},
		methods: {
			kk:function(){
				var formdata = new FormData()
				formdata.append('file',this.ff[0])
				let config = {
					headers: {
						'Content-Type': 'multipart/form-data'
					},
					params:this.form
				}
				axios.post("http://localhost:8080/upload",formdata,config).then(function(res){
					console.log(res.data)
				})
			},
			tirggerFile: function(event) {
				var file = event.target.files;
				this.ff = file;
				console.log(this.ff)
			},
			ck: function(data, datas) {
				console.log(datas.checkedKeys)
				axios.get("http://localhost:8080/dels?ids=" + datas.checkedKeys).then(function(res) {
					console.log('o')
				}).catch(function(res) {})
			},
			upd: function(node, data) {
				this.$parent.$refs.upd.dialogFormVisible = true
				this.$parent.$refs.upd.form = JSON.parse(JSON.stringify(data));
			},
			add: function(node, data) {
				console.log(data)
				this.$refs.add.dialogFormVisible = true
				this.$refs.add.form = data
			},
			remove: function(node, data) {
				var thiz = this
				axios.get("http://localhost:8080/del/" + data.id).then(function(res) {
					if (res.data) {
						thiz.$message({
							message: '删除成功',
							type: 'success'
						});
						thiz.dialogFormVisible = false
						location.reload()
					}
				})
			},
			filterNode(value, data) {
				if (!value) return true;
				return data.name.indexOf(value) !== -1;
			},
			handleCheckChange(data, checked, indeterminate) {
				//console.log(data, checked, indeterminate);
			},
			handleNodeClick(data) {
				console.log(data);
			},
			async loadNode(node, resolve) {
				if (node.level === 0) {
					var rest
					rest = await axios.get("http://localhost:8080/find/0");
					$(rest.data).each(function(i, v) {
						v.disabled = true
					})

					return resolve(rest.data);
				} else {
					var rest
					rest = await axios.get("http://localhost:8080/find/" + node.data.id);

					resolve(rest.data);
				}

			},
			/* reloadNode:function(node)
			{
				axios.get("http://localhost:8080/find/0").then(function(res){
					node.childNodes=res.data
				})
			}, */
			show: function() {
				console.log("=================")
				console.log(node)
				console.log("=================")
				console.log(resolve)
			},
			getCheckedNodes: function() {
				console.log(this.$refs.tree.getCheckedNodes());
			},
			handleRemove(file, fileList) {
				console.log(file, fileList);
			},
			handlePreview(file) {
				console.log(file);
			},
			handleExceed(files, fileList) {
				this.$message.warning(`当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${files.length + fileList.length} 个文件`);
			},
			beforeRemove(file, fileList) {
				return this.$confirm(`确定移除 ${ file.name }？`);
			}
		},
		mounted: function() {

		}
	};
</script>
<style>
	.custom-tree-node {
		flex: 1;
		display: flex;
		align-items: center;
		justify-content: space-between;
		font-size: 14px;
		padding-right: 8px;
	}
</style>
