<template>
	<div>
		<el-tree :data="list" :props="defaultProps" @node-click="handleNodeClick">
			<span class="custom-tree-node" slot-scope="{ node, data }">
				<span>{{ node.label }}</span>
				<span>
					<el-button type="text" size="mini" @click="() => append(data)">
						Append
					</el-button>
					<el-button type="text" size="mini" @click="() => remove(node, data)">
						Delete
					</el-button>
				</span>
			</span>
		</el-tree>
		<button @click='show'></button>
	</div>
</template>

<script>
	import axios from 'axios'
	import $ from 'jquery'

	export default {
		name: 'tree',
		data() {
			return {
				list: [],
				defaultProps: {
					children: 'children',
					label: 'name'
				}
			};
		},
		methods: {
			find: function() {
				var thiz = this
				axios.get("http://localhost:8080/find/0").then(function(res) {
					thiz.list = res.data
				})
			},
			handleNodeClick: function(data) {
				var thiz = this
				Vue.delete(thiz.list,data)
			},
			show: function() {
				this.list = this.data
			},
			append(data) {
				console.log(data)
			},

			remove(node, data) {
				console.log(data)
				this.list.splice(data,1)
			},

		},
		mounted: function() {
			this.find()
		}
	}
</script>

